body{padding:0;box-sizing:border-box;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#15212b}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

nav.escritorio{background-color:#0D1321;display:flex;flex-direction:row-reverse;align-items:center;justify-content:space-evenly;color:#fff;height:8vh;width:30%}nav.movil{display:none}button.movil{display:none}@media screen and (max-width: 800px){button.movil{background-color:transparent;border:none;display:block;outline:none;width:10%}button.movil svg{font-size:large;color:white}nav.movil{transition:all 0.4s;background-color:#0D1321;overflow:hidden;display:flex;position:absolute;flex-direction:column;align-items:center;justify-content:space-evenly;border-bottom:1px solid;color:#fff;margin-top:8vh;height:0;width:100vw;border:none}nav.movil button:last-child{border-bottom:2px solid #748CAB}nav.movil button:hover{height:inherit;margin-top:0;margin-top:initial}nav.escritorio{display:none}}

button.nav{background-color:transparent;height:100%;overflow:hidden;font-family:sans-serif;font-size:0.8em;width:100%;background-color:#0D1321;text-transform:capitalize;color:#3E5C76;font-weight:bold;border:none;transition:all .5s;text-overflow:ellipsis}button.nav svg{margin:0 1rem}button.nav:active{border:1px solid #38444d;outline:none}button.nav:hover{height:102.5%;width:110%;margin-top:1.5%;box-shadow:2px 2px 2px black}

header{width:100%;display:flex;height:8vh;flex-direction:row-reverse;background-color:#0D1321;justify-content:space-between}header img{overflow:hidden;margin:0 4%;max-height:80%;max-width:80%}header h1{box-sizing:content-box;text-align:center;align-self:center;margin-top:2%;display:block;width:20%;height:100%;text-transform:capitalize;color:#748CAB}@media screen and (max-width: 800px){header{width:100%;display:flex;height:8vh;flex-direction:row-reverse;background-color:#0D1321;justify-content:space-between}header img{overflow:hidden;margin:0 4%;max-height:80%;max-width:80%}header h1{font-size:medium;align-self:center;padding:2%;margin:0;margin-top:2%;display:block;width:20%;height:100%;align-content:center;text-transform:capitalize;color:#748CAB}}

body{padding:0;box-sizing:border-box;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#15212b}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.contenedor-buscador{width:25%;display:flex;flex-flow:row-reverse;align-items:center;justify-content:flex-start}.contenedor-buscador button{background-color:transparent;border:none;height:50%;color:white;cursor:pointer}.contenedor-buscador input{transition:.8s;box-sizing:border-box;transition:.4s;height:50%;border:none;outline:none;border-bottom:3px solid white;width:15%;margin:1%;background-color:transparent;text-align:center;color:transparent}.contenedor-buscador input::-webkit-input-placeholder{-webkit-transition:all .4s;transition:all .4s;display:none;text-transform:capitalize;color:transparent;text-align:center}.contenedor-buscador input:-ms-input-placeholder{-ms-transition:all .4s;transition:all .4s;display:none;text-transform:capitalize;color:transparent;text-align:center}.contenedor-buscador input::-ms-input-placeholder{-ms-transition:all .4s;transition:all .4s;display:none;text-transform:capitalize;color:transparent;text-align:center}.contenedor-buscador input::placeholder{transition:all .4s;display:none;text-transform:capitalize;color:transparent;text-align:center}.contenedor-buscador input:focus{color:white;height:50%;border:none;outline:none;border-bottom:3px solid white;width:90%;margin:1%;background-color:transparent}.contenedor-buscador input:focus::-webkit-input-placeholder{color:white}.contenedor-buscador input:focus:-ms-input-placeholder{color:white}.contenedor-buscador input:focus::-ms-input-placeholder{color:white}.contenedor-buscador input:focus::placeholder{color:white}@media screen and (max-width: 800px){.contenedor-buscador input{transition:.8s;box-sizing:border-box;transition:.4s;height:50%;border:none;outline:none;border-bottom:3px solid white;width:100%;margin:1%;background-color:transparent;text-align:center;color:white}}

